import { Component, Element, Prop, h } from '@stencil/core';
import { SharedStyles } from '../../utils/shared-styles';

@Component({
  tag: 'storm-html-view',
  styleUrl: 'storm-html-view.css',
  shadow: true,
})
export class StormHtmlView {
  @Element() host: HTMLElement;
  @Prop() url: string;
  @Prop() width: string = '100%';
  @Prop() height: string = '100%';
  @Prop() viewTitle: string;
  @Prop() omitInlineStyles: boolean = false;

  componentWillLoad() {
    SharedStyles.applyShadowRootStyles(this.host);
  }

  render() {
    if (!this.url) {
      console.log('URL is not valid');
      return;
    }
    let style: any = {};
    if (!this.omitInlineStyles) {
      style.width = this.width;
      style.height = this.height;
      style.overflow = 'auto';
      style.border = 'none';
    }
    return <iframe title={this.viewTitle} src={this.url} allow="fullscreen" style={style} />;
  }
}
